import { Injectable } from '@angular/core';
import { Usuario } from '../models/usuario';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http'; 

@Injectable({
  providedIn: 'root'
})

export class ServService {

  private REST_API_SERVER:string = "http://213.27.151.153:8081/api/usuarios";

  private httpHeaders:HttpHeaders = new HttpHeaders()
    .set('Content-Type', 'application/json')
    .set('Access-Control-Allow-Methods','GET, POST, PUT, DELETE')
    .set('Access-Control-Allow-Origin', '*');     

  constructor(private httpClient: HttpClient) {  }

  getUsers(){
    return this.httpClient.get(this.REST_API_SERVER, { headers: this.httpHeaders });
  }

  addUser(user:Usuario){ 
    return this.httpClient.post(this.REST_API_SERVER, user, { headers: this.httpHeaders });
  }

  updateUser(user:Usuario){  
    return this.httpClient.put(this.REST_API_SERVER + '/' + user.id, user, { headers: this.httpHeaders });
  }

  deleteUser(id:string){  
    return this.httpClient.delete(this.REST_API_SERVER + '/' + id, { headers: this.httpHeaders });
  }
}