import { Component } from '@angular/core';
import { ServService } from '../app/services/serv.service';
import { Resposta } from './models/resposta';
import { Usuario } from './models/usuario';
import * as XLSX from 'xlsx'; 

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent {

  users_json:Usuario; //variable per carregar el grid
  id:string;

  public frmNom; 
  public frmEdat; 
  public frmAdre; 

  public is_all_form_ok = true;

  constructor(private data: ServService) {   }

  ngOnInit() { 
    this.nou();
    this.updateGrid();
  }  

  updateGrid(){
      this.data.getUsers().subscribe((response:Usuario)=> {
        this.users_json = response;          
      });
  }

  guardar() { 

    let user:Usuario; //variable per obtenir les dades del formulari

    let sNom:string = this.frmNom;
    let sEdat:string = this.frmEdat;
    let sAdre:string = this.frmAdre;

    if(sNom.trim() == "")
    {
        this.is_all_form_ok = false;
        return;
    }

    if(sEdat.trim() == "")
    {
        this.is_all_form_ok = false;
        return;
    }

    if(sAdre.trim() == "")
    {
        this.is_all_form_ok = false;
        return;
    }
   
    user = {id: this.id, nom: sNom, edat: sEdat, adre: sAdre}

    let res;  

    if(this.id=="")    
      res = this.data.addUser(user);
    else
      res = this.data.updateUser(user);

    res.subscribe((response:Resposta)=> {    
      if (response.txt.startsWith('OK')) 
      {
        this.updateGrid();
        this.nou();
      }
    });
  }

  nou()
  {
    this.id = ""
    this.frmNom = '';
    this.frmEdat = '';
    this.frmAdre = '';
    this.is_all_form_ok = true;
  }

  pujarDades(event, xid:string, nom:string, edat:string, adre:string){
    this.id = xid
    this.frmNom = nom;
    this.frmEdat = edat;
    this.frmAdre = adre;
  }

  deleteUser(event, xid:string){  

    //let modal: NgbActiveModal;

    //modal.close('Oddk clicddk');

    let res = this.data.deleteUser(xid);  

    res.subscribe((response:Resposta)=> {    
      if (response.txt.startsWith('OK')) 
      {
        this.updateGrid();
        this.nou();
      }
    });
  }

  export(){
    {
      /* table id is passed over here */   
      let element = document.getElementById('excel-table'); 
      const ws: XLSX.WorkSheet =XLSX.utils.table_to_sheet(element);

      /* generate workbook and add the worksheet */
      const wb: XLSX.WorkBook = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');

      let filename="excel.xlsx";
      /* save to file */
      XLSX.writeFile(wb, filename);
     
   }
  }
}